import './index.scss';

import React, { useContext, } from 'react';
import { useTranslation, } from 'react-i18next';

import { ThemeContext, } from '../theme';
import Page from '../components/Page';
import ScreenStart from '../screens/ScreenStart';

import { ArrowLeftIcon, } from '../components/Svg';
import { Link, } from '../components/Link';



export default function ServicesPage() {
  const { theme, } = useContext(ThemeContext);
  const { i18n, t, } = useTranslation('services');

  const imagePrefix = "https://ivannikov.pro";
  const imageUrl = imagePrefix + `/ivannikov.pro_${i18n.language}.jpg`;

  return (
    <Page
      title={t('Services')}
      keywords={t('keywordsList')}
      meta={[
        { name: 'image', content: imageUrl, },
        { name: 'og:image', property: 'og:image', content: imageUrl, },
        { name: 'twitter:image', property: 'twitter:image', content: imageUrl, },
      ]}
    >
      <div id={`front ${theme}`} style={{ minHeight: '95vh', }}>
        <main className='main'>
          <ScreenStart
            preTitle={t('Aleksandr Ivannikov')}
            title={t('Services')}
            description={t('Services')}
          //descriptionComponent={}
          //imageComponent={}
          />
        </main>
      </div>
    </Page>
  );
}
